a {
  color: #4a4a4a;
  text-decoration: none;
  transition: color 0.3s ease;
}

a:hover {
  color: #158d11;
}

.navbar-light .navbar-brand {
  color: #158d11;
}

.navbar-light .navbar-nav .nav-link {
  color: #4a4a4a;
}

.navbar-light .navbar-nav .nav-link:hover {
  color: #158d11;
}
